import React, { useEffect } from 'react';
import { Form, Row, Col, Select, Checkbox } from 'antd';
import { BtnSearch } from 'components/common/button';
import FilterPathEducate from 'components/common/input/FilterPathEducate';
import { getYearEducations } from 'services/yearEducationService';
import { SelectAjax } from 'components/common/index';
import { udpateQueryParams, getQueryParams } from 'utils/urlUtil';
import { requiredRule } from 'utils/validateRules';

const FormSearch = props => {
  const { Option } = Select;

  useEffect(() => {
    const params = getQueryParams();
    delete params._time;
    params.includeRetest = params.includeRetest === 'true';
    props.form.setFieldsValue(params);
  }, []);

  const handleSearch = e => {
    e.preventDefault();
    props.form.validateFields((err, values) => {
      udpateQueryParams(values);
    });
  };

  return (
    <Form className="form-inline-label" layout="vertical" onSubmit={handleSearch}>
      <Row gutter={24}>
        <FilterPathEducate form={props.form} />
        <Col span={5}>
          <Form.Item label="Năm học">
            {props.form.getFieldDecorator('yearEduId', { rules: [requiredRule] })(
              <SelectAjax
                service={getYearEducations}
                setLabel={yearEdu => `${yearEdu.fromYear}-${yearEdu.toYear}`}
                typeValue="int"
              />
            )}
          </Form.Item>
        </Col>
        <Col span={5}>
          <Form.Item label="Học kỳ" required>
            {props.form.getFieldDecorator('semesterId', { rules: [requiredRule] })(
              <Select>
                <Option value="1">Học kỳ 1</Option>
                <Option value="2">Học kì 2</Option>
              </Select>
            )}
          </Form.Item>
        </Col>
        <Col span={5}>
          <Form.Item required>
            {props.form.getFieldDecorator('includeRetest', {
              valuePropName: 'checked',
            })(<Checkbox>Tính cả điểm học lại</Checkbox>)}
          </Form.Item>
        </Col>
        <Col span={5}>
          <div className="btn-group">
            <BtnSearch htmlType="submit" />
          </div>
        </Col>
      </Row>
    </Form>
  );
};

export default Form.create()(FormSearch);



